<!--
 * @Descripttion: OID
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 22:42:04
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:12:48
-->
<template>
    <div class="devices">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="OidAdd = !OidAdd"></el-button>
                </el-form-item>
            </el-form>
            
        </div>
        <div class="message-table">
            <el-table
                ref="record-table"
                :data="list"
                style="width: 100%"
                stripe
                :height = "tableHeight"
                highlight-current-row
                v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label=""
                prop="serverName"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                prop="serverType"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="Oid"
                prop="oid"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                prop="type"
                show-overflow-tooltip
                >
                    <template slot-scope="scope">
                        <div class="dedevice-run">
                            <p v-if="scope.row.type == 1"></p>
                            <p v-if="!scope.row.type == 1"></p>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                >
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value='1'
                            :inactive-value='2'
                            @change="changeStatus(scope)"
                            >
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                prop="created_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                prop="updated_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label=""
                fixed="right"
                width="200"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);"></el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteSnmpOidIndexList(scope);"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
       <el-dialog width="50%" :visible.sync="OidAdd" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <OidAdd v-on:freshData="freshData"></OidAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="OidEdit" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <OidEdit v-on:freshData="freshData" v-if="OidEdit" :editId.sync='editId'></OidEdit>
        </el-dialog>
    </div>
</template>

<script>
    import OidAdd from './add/add.vue'
    import OidEdit from './add/edit.vue'
    export default{
        data(){
            return{
                list:[],//
                tableHeight:600, // 
                OidAdd:false,
                OidEdit:false,
                editId:'',
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableLoading:false, //
                editId:'', //id
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            getSnmpOidIndexList(){
                //
                this.tableLoading = true; //
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                }
                capis.getSnmpOidIndexList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //
                        this.list = data.res.data;
                        //
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 
                if(data && data.type == 'cancel')
                {
                    // ,
                    
                }
                else if(data && data.type == 'update')
                {
                    // 
                    this.getSnmpOidIndexList();
                }
                else if(data && data.type == 'add')
                {
                    // 
                    //1
                    this.pagenation.page = 1;
                    this.getSnmpOidIndexList();
                }
                this.OidAdd = false;
                this.OidEdit = false;
            },
            sizeChange(pagesize){
                //
                this.pagenation.pagesize = pagesize;
                this.getSnmpOidIndexList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.getSnmpOidIndexList();
            },
            deleteSnmpOidIndexList(data)
            {
                // 
                    this.$confirm(''+data.row.serverName+'?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                            id:data.row.id
                        }
                        capis.deleteSnmpOidIndexList(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //
                                this.list.splice(data.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '!'
                                });
                            }else{
                                this.$notify({
                                    title: '',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '',
                                message: '',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
            //
            changeStatus(data)
            {
                console.log(12312,data)
                this.formLoading=true;
                    //
                    var params = {
                        act:'changeStatus',
                        id:data.row.id,
                        status:data.row.status
                    }
                capis.saveSnmpOidIndexList(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                        });
                    }else{
                        this.$notify({
                            title: '',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                })
            },
            editInfo(data)
            {
                this.editId = data.row.id;
                this.OidEdit = true ;
            },
        },
        created(){
            //
            this.getSnmpOidIndexList();
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            });
        },
        components:{
            OidAdd,
            OidEdit
        },
    }
</script>

<style scoped lang="scss">
    .devices{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
        .dedevice-run{
            display: flex;
            align-items: center;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>



